import { products } from '../components/products/products';

const initialState = {
    productArr: products,
    modalBackDrop: false,
    loginForm: false,
    signupForm: false,
    login: {
        email: '',
        password: '',
    },
    signup: {
        firstname: '',
        lastname: '',
        email: '',
        password: '',
        confirmPassword: '',
    },
}

const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'SET_PRODUCTS':
            state = {
                ...state,
                productArr: action.payload
            }
            break;
        case 'ENABLE_BACKDROP':
            state = {
                ...state,
                modalBackDrop: true
            }
            break;
        default:
        break;
    }
    return state;
}

export default Reducer;